import VueRouter from 'vue-router';

import HomePage from '../views/HomePage.vue';
import UploadVideo from '../views/UploadVideoPage';
import LoginPage from '../views/LoginPage';
import UserPage from '../views/UserPage';
// import SearchPage from '../views/SearchPage';
import AdminPage from '../views/AdminPage';

function routes(store) {
  return {
    home: {
      name: 'home',
      path: '/',
      component: HomePage,
    },
    user: {
      name: 'user',
      path: '/user/',
      component: UserPage,
      beforeEnter: (to, from, next) => {
        if (store.getters['authMod/isLoggedIn']) {
          next();
        } else {
          next({name: 'login'});
        }
      },
    },
    uploadVideo: {
      name: 'uploadVideo',
      path: '/uploadVideo',
      component: UploadVideo,
      beforeEnter: (to, from, next) => {
        const getter = store.getters['authMod/isLoggedIn'];
        if (getter) {
          next();
        } else {
          next({name: 'login'});
        }
      },
    },
    // search: {
    //   name: 'search',
    //   path: '/search/:searchQuery',
    //   component: SearchPage,
    // },
    admin: {
      name: 'admin',
      path: '/admin',
      component: AdminPage,
      beforeEnter: (to, from, next) => {
        if (store.getters['authMod/isLoggedIn']) {
          next();
        } else {
          next({name: 'login'});
        }
      },
    },
    login: {
      name: 'login',
      path: '/login',
      component: LoginPage,
      beforeEnter: (to, from, next) => {
        if (store.getters['authMod/isLoggedIn']) {
          next({name: 'uploadVideo'});
        } else {
          next();
        }
      },
    },
  };
}

export function useVueRouter(app) {
  app.use(VueRouter);
}

export {routes};

let routerModule = (function () {
  let instance;
  let createInstance = function (store) {
    return new VueRouter({
      mode: 'history',
      routes: [
        routes(store).home,
        // routes(store).user,
        // routes(store).uploadVideo,
        routes(store).login,
        // routes(store).search,
        routes(store).admin,
      ],
    });
  }

  return {
    getInstance: function (store) {
      return instance || (instance = createInstance(store));
    }
  }
})();

export default routerModule;

